/* CSS Document */

@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Bree+Serif);

body {
	background: #212121;
	font-size:22px;
	line-height: 32px;
	color: #ffffff;
	margin: 0;
	padding: 0;
	word-wrap:break-word !important;
  font-family: 'Open Sans', sans-serif;
  width: 100%;
	}

h1 {
	font-size: 55px;
	color: #FFF;
}	

/* h3 {
	font-size: 30px;
	line-height: 34px;
	text-align: center;
	color: #FFF;
} */

h3 a {
	color: #FFF;
}

a {
	color: #FFF;
}

h1 {
	line-height: 50px;
	}

#container {
	margin: 0 auto;
	max-width: 890px;
}

p {
	text-align: center;
}

.toggle,
[id^=drop] {
	display: none;
}

/* Giving a background-color to the nav container. */
nav { 
	margin:0;
	padding-top: 10px;
  background: 
  linear-gradient(
    rgba(0, 0, 0, 0.15), 
    rgba(0, 0, 0, 0.15)),
  url('./photos/sarape.png');
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: 50%;
  background-position-y: 0%;
  border-bottom: 2px solid green;
}

#logo {
	display: block;
  padding-left: 30px;
	float: left;
	font-size:20px;
}

/* Since we'll have the "ul li" "float:left"
 * we need to add a clear after the container. */

nav:after {
	content:"";
	display:table;
	clear:both;
}

/* Removing padding, margin and "list-style" from the "ul",
 * and adding "position:reltive" */
nav ul {
	float: right;
	padding:0;
	margin:0;
	list-style: none;
	position: relative;
	}
	
/* Positioning the navigation items inline */
nav ul li {
	margin: 0px;
	display:inline-block;
	float: left;
	background-color: transparent;
	}

/* Styling the links */
nav a {
	display:block;	
	color:#FFF;
	font-size:17px;
	text-decoration:none;
}



/* Hide Dropdowns by Default
 * and giving it a position of absolute */
nav ul ul {
	display: none;
	position: absolute; 
	/* has to be the same number as the "line-height" of "nav a" */
	top: 60px; 
}
	
/* Display Dropdowns on Hover */
nav ul li:hover > ul {
	display:inherit;
}
	


/* Media Queries
--------------------------------------------- */

@media all and (max-width : 768px) {

	#logo {
		display: block;
		padding: 0;
		width: 100%;
		text-align: center;
		float: none;
	}

	nav {
		margin: 0;
	}

	/* Hide the navigation menu by default */
	/* Also hide the  */
	.toggle + a,
	.menu {
    display: none;
    margin-top: 10px;
	}

	/* Stylinf the toggle lable */
	.toggle {
		display: block;
		background-color: transparent;
		padding:14px 20px;	
		color:#FFF;
		font-size:18px;
		text-decoration:none;
    border:none;
    text-align: center;
    text-shadow:
    -1px -1px 0 green,  
     1px -1px 0 green,
     -1px 1px 0 green,
      1px 1px 0 green; 
    font-weight: 700;
	}


	/* Display Dropdown when clicked on Parent Lable */
	[id^=drop]:checked + ul {
    display: block;
    background: rgba(0, 0, 0, 0.75);
    width: 100%;
	}

	/* Change menu item's width to 100% */
	nav ul li {
		display: block;
		width: 100%;
		}

	nav ul ul .toggle,
	nav ul ul a {
		padding: 0 40px;
	}

	nav ul ul ul a {
		padding: 0 80px;
	}

  
	nav ul li ul li .toggle,
	nav ul ul a,
  nav ul ul ul a{
		padding:14px 20px;	
		color:#FFF;
		font-size:17px; 
	}
  
  
	nav ul li ul li .toggle,
	nav ul ul a {
		background-color: #212121; 
	}

	/* Hide Dropdowns by Default */
	nav ul ul {
		float: none;
		position:static;
		color: #ffffff;
		/* has to be the same number as the "line-height" of "nav a" */
	}
		
	/* Hide menus on hover */
	nav ul ul li:hover > ul,
	nav ul li:hover > ul {
		display: none;
	}
		
	/* Fisrt Tier Dropdown */
	nav ul ul li {
		display: block;
		width: 100%;
	}

	nav ul ul ul li {
		position: static;
		/* has to be the same number as the "width" of "nav ul ul li" */ 

	}

}

@media all and (max-width : 330px) {

	nav ul li {
		display:block;
		width: 94%;
	}

}